Component, ref } from \"vue\";\nimport { container } from \"../../../../domain/core/diContainer\";\nimport { IApplication } from \"../../../../domain/model/models\";\nimport { IRouter } from \"../../../../domain/presentation/IRouter\";\nimport { IObjectUseCase } from \"../../../../domain/useCase/IObjectUseCase\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport \"bootstrap/js/dist/dropdown\";\nimport { IDialog } from \"../../../../domain/presentation/IDialog\";\n\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst objectUseCase = container.resolve<IObjectUseCase>(IObjectUseCase);\nconst router = container.resolve<IRouter>(IRouter);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst props = defineProps<{ app: IApplication }>();\n\nlet options: any = { month: \"2-digit\", day: \"2-digit\", year: \"numeric\" };\n\nfunction openApp() {\n    router.push(`/studio/${props.app.ID}`);\n}\n\nfunction updateApp() {\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../../components/application/addOrUpdateApp.vue`)),\n        { closable: true, title: \"Update Application\" },\n        { app: props.app }\n    );\n}\n\nfunction deleteApp() {\n    executor.execute(\n        async () => {\n            await objectUseCase.deleteObject(props.app.ID);\n        },\n        { loading: true }\n    );\n}\n</script>\n<template>\n    <div class=\"card each-card\">\n        <div class=\"card-image-container\">\n            <div class=\"hover-item\" @click=\"openApp()\"></div>\n            <span class=\"hover-item-text\">OPEN IN STUDIO</span> \n            <i class=\"mdi mdi-arrow-right mdi-18px hover-item-icon\"></i>\n            <img v-if=\"app.logo\" :src=\"app.logo\" class=\"card-logo\" />\n            <img v-else src=\"../../../../images/ide5-logo.svg\" class=\"ide-logo\" />\n        </div>\n        <div class=\"card-content-container\">\n            <div class=\"app-name ellipsis\" :title=\"app.name\">{{ app.name }}</div>\n            <div class=\"app-info-text ellipsis\" style=\"top: 37px\" :title=\"app.createdBy\">{{ app.createdBy || \"\" }}</div>\n            <span v-if=\"app.createDate\" class=\"app-info-text\" style=\"top: 58px\">{{ new Intl.DateTimeFormat(\"tr-TR\", options).format(new Date(app.createDate)) }}</span>\n            <i\n                class=\"mdi mdi-dots-vertical vertical-dot-icon\"\n                id=\"dropdownMenuButton1\"\n                data-bs-toggle=\"dropdown\"\n                aria-expanded=\"false\"\n                data-bs-target=\"#dropdown-menu\"\n            ></i>\n            <ul class=\"dropdown-menu\" style=\"min-width: 92px\" aria-labelledby=\"dropdownMenuButton1\">\n                <li class=\"dropdown-item dropdown-each-item\" @click=\"updateApp()\">\n                    <i class=\"mdi mdi-pencil-outline dropdown-icon\"></i><span class=\"dropdown-text\">Edit</span>\n                </li>\n                <li class=\"dropdown-item dropdown-each-item\" style=\"color: #e96767\" @click=\"deleteApp()\">\n                    <i class=\"mdi mdi-delete-outline dropdown-icon\"></i><span class=\"dropdown-text\">Delete</span>\n                </li>\n            </ul>\n        </div>\n    </div>\n</template>\n\n<style>\n@import url(\"../../../../styles/IDE3.css\");\n</style>\n<style scoped>\n.card-image-container {\n    position: relative;\n    cursor: pointer;\n    width: 278px;\n    height: 198px;\n    border-radius: 3px 3px 0px 0px;\n    border-bottom: 1px solid #d5d5d5;\n}\n\n.hover-item {\n    position: absolute;\n    background: #212121 0% 0% no-repeat padding-box;\n    border-radius: 3px 3px 0px 0px;\n    opacity: 0;\n    width: 278px;\n    height: 195px;\n    cursor: pointer;\n    text-align: center;\n    padding-top: 99px;\n    visibility: hidden;\n    z-index: 1;\n}\n\n.card-image-container:hover .hover-item {\n    visibility: visible;\n    opacity: 0.6;\n    transition: visibility 0s, opacity 0.1s ease-in-out;\n}\n\n.hover-item-text {\n    position: absolute;\n    top: 87px;\n    left: 59px;\n    z-index: 2;\n    font: normal normal normal 18px/24px Roboto;\n    color: #ffffff;\n    visibility: hidden;\n}\n\n.hover-item-icon {\n    position: absolute;\n    top: 85px;\n    left: 199px;\n    color: #ffffff;\n    z-index: 2;\n    visibility: hidden;\n}\n\n.card-image-container:hover .hover-item-text {\n    visibility: visible;\n}\n\n.card-image-container:hover .hover-item-icon {\n    visibility: visible;\n}\n\n.ide-logo {\n    position: absolute;\n    top: calc((100% - 64px) / 2);\n    left: calc((100% - 61px) / 2);\n    width: 61px;\n    height: 64px;\n}\n\n.card-content-container {\n    width: 278px;\n    height: 94px;\n    border-radius: 3px 3px 0px 0px;\n    position: relative;\n}\n\n.ellipsis {\n    white-space: nowrap;\n    overflow: hidden !important;\n    text-overflow: ellipsis;\n}\n\n.app-name {\n    position: absolute;\n    top: 12px;\n    left: 12px;\n    right: 12px;\n    font-size: 16px;\n    font-weight: 500;\n    font-family: \"Roboto\";\n    color: #151233;\n}\n\n.app-info-text {\n    position: absolute;\n    left: 12px;\n    font-weight: 400;\n    font-size: 14px;\n    font-family: \"Roboto\";\n    color: #757575;\n}\n\n.vertical-dot-icon {\n    position: absolute;\n    top: 50px;\n    left: 244px;\n    color: #757575;\n    cursor: pointer;\n}\n\n.dropdown-each-item {\n    position: relative;\n    padding: 8px 8px 6px 8px;\n    cursor: pointer;\n}\n\n.dropdown-text {\n    font: normal normal normal 14px/19px Roboto;\n    margin: 6px;\n}\n\n.dropdown-icon {\n    position: inherit;\n    top: 1px;\n}\n\n.block {\n    background-color: white;\n    opacity: 0.8;\n    height: 246px;\n    width: 332px;\n    position: absolute;\n    z-index: 1;\n}\n</style>\n"],"sourceRoot":""}]);
